// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  checkGetListShiftByDeviceCodeAction,
  checkGetCheckListByDateAndDeviceCodeAction,
  checkReview,
  checkUnReview
} from '@/api/checkConfig';
import {
  configurationGetConfigurationByEnum
} from '@/api/system.js';
import {
  showSuccessMessage,
  showWarningMessage
} from '@/utils/message';
import { VARIABLE, BTN_TEXT, CONSTANTS_CHECK, STATUS_CODE, TITLE, DEVICE_CHECK, CONSTANTS_CODE, IP, FILE_TYPE } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
    // 设备code
    deviceCode: {
      type: String,
      default: ''
    },
    // 年月日
    yearMonthDay: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_CODE: CONSTANTS_CODE,
      DEVICE_CHECK: DEVICE_CHECK,
      selectedShift: null,
      options: [],
      // 工作描述
      checkRemarks: '',
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'seq',
          title: VARIABLE.SEQ,
          align: 'center'
        },
        {
          field: 'itemName',
          title: CONSTANTS_CHECK.CHECK_ITEM,
          align: 'center'
        },
        {
          field: 'checkState',
          title: CONSTANTS_CHECK.IS_FINISH,
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.checkState === 0) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 1)}
                    class="icon-unChecked"
                  ></i>
                ];
              } else if (row.checkState === 1) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 2)}
                    class="icon-checked"
                  ></i>
                ];
              } else if (row.checkState === 2) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 0)}
                    class="icon-no"
                  ></i>
                ];
              } else if (row.checkState === 3) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 1)}
                    class="icon-stop"
                  ></i>
                ];
              } else if (row.checkState === 4) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 1)}
                    class="icon-holiday"
                  ></i>
                ];
              }
            }
          }
        },
        {
          field: 'value',
          title: CONSTANTS_CHECK.REAL_VALUE,
          align: 'center'
        }
      ],
      // 图片上传
      relationshipId: '',
      dialogImageUrl: '',
      fileList: [],
      delFileList: [],
      dialogVisible: false,
      uploadConfig: {
        headers: {
          token: window.localStorage.getItem('token').replace(/"/g, '')
        },
        url: IP.PUBLICIP + 'fileResource/uploadOneFile.action',
        data: {
          fileResourceTypeEnum: FILE_TYPE.CHECK_RECORD_IMAGE,
          relationshipId: ''
        }
      },
      canReviewCheck: false,
      centerDialogVisible: false,
      checkRecord: {}
    };
  },
  computed: {},
  created() {
  },
  mounted() {
    // this.searchItem();
  },
  methods: {
    toggleFinish(row, value) {
      row.isFinish = value;
    },
    /**
     * @description: 获取班次数据
     * @param {type}
     * @return:
     */
    async searchItem() {
      this.tableDatas = [];
      this.options = [];
      this.selectedShift = null;
      if (this.deviceCode === '') {
        showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
        return false;
      }
      try {
        const res = await checkGetListShiftByDeviceCodeAction({
          deviceCode: this.deviceCode,
          date: this.yearMonthDay + ' 00:00:00'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.options = res.rows.reverse();
          this.selectedShift = res.rows[0].shiftId;
          this.showItem();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询点检项
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async showItem() {
      this.fileList = [];
      this.checkRemarks = '';
      try {
        const res = await checkGetCheckListByDateAndDeviceCodeAction({
          deviceCode: this.deviceCode,
          date: this.yearMonthDay + ' 00:00:00',
          shiftId: this.selectedShift
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatas = res.rows.checkList;
          this.checkRecord = res.rows.checkRecord;
          this.checkRemarks = res.rows.checkRecord.checkInformation;
          this.showReviewCheck();
          this.getFileList(res.rows.checkImageList);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('toggleHistroyItem');
    },
    /**
    * @description: 图片控制
    * @param 无
    * @return:无
    * @author: ZJM
    */
    handleRemove(file, fileList) {
      if (!file.flag) {
        this.delFileList.push(file.response.rows.resourceId);
        this.fileList = fileList;
      } else {
        this.delFileList.push(file.id);
        this.fileList = fileList;
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    updateFiles() {
      if (!this.deviceCode) {
        showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
        return false;
      }
    },
    updateSuccess(response, file, fileList) {
      this.fileList.push(file);
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        // this.fileList.push({
        //   id: response.rows.resourceId,
        //   url: IP.PUBLICIP + response.rows.fileUrl,
        //   relationshipId: response.rows.relationshipId
        // });
      }
    },
    getFileList(imgFIle) {
      const arr = imgFIle.map(item => {
        var json = {
          id: item.resourceId,
          url: IP.PUBLICIP + item.fileUrl,
          relationshipId: item.relationshipId,
          flag: true
        };
        return json;
      });
      this.delFileList = [];
      this.fileList = arr;
    },
    async showReviewCheck() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'CHECK_REVIEW'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.isEnable === 1) {
            this.canReviewCheck = true;
          } else {
            this.canReviewCheck = false;
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    checkReview() {
      if (this.checkRecord.checkState === 0) {
        showWarningMessage('未完成点检，无法进行审核！');
      } else {
        this.centerDialogVisible = true;
      }
    },
    async checkReviewPass() {
      try {
        const data = await checkReview({}, [this.checkRecord.recordId]);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.centerDialogVisible = false;
          showSuccessMessage(data.message);
          this.showItem();
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async checkReviewNoPass() {
      try {
        const data = await checkUnReview({}, [this.checkRecord.recordId]);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.centerDialogVisible = false;
          showSuccessMessage(data.message);
          this.showItem();
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    reviewText() {
      if (this.checkRecord.reviewState) {
        if (this.checkRecord.reviewState === 1) {
          return '【' + this.checkRecord.reviewedByName + '】审核通过';
        } else {
          return '【' + this.checkRecord.reviewedByName + '】审核不通过';
        }
      } else {
        return '未审核';
      }
    }
  }
};
