// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import moment from 'moment';
import {
  checkGetListDeviceCheckHistoryAction
} from '@/api/checkConfig';
import {
  configurationGetConfigurationByEnum
} from '@/api/system.js';
import { showWarningMessage } from '@/utils/message';
import {
  TITLE, STATUS_CODE,
  DEVICE_CHECK,
  VARIABLE,
  BTN_TEXT
} from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
    // 设备code
    deviceCode: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      VARIABLE: VARIABLE,
      month: '',
      // table数据
      tableDatas: [],
      canReviewCheck: false
    };
  },
  computed: {},
  created() {
    this.month = moment(new Date().getTime()).format('YYYY-MM');
  },
  mounted() {

  },
  methods: {
    toggleFinish(row, value) {
      row.isFinish = value;
    },
    /**
     * @description: 获取点检历史数据
     * @param 无
     * @return:无
     */
    async searchItem() {
      this.showReviewCheck();
      this.tableDatas = [];
      if (this.deviceCode === '') {
        showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
        return false;
      }
      try {
        const res = await checkGetListDeviceCheckHistoryAction({
          deviceCode: this.deviceCode,
          yearMonth: this.month
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 显示历史详情
     * @param val 历史数据
     * @return: 无
     * @author: ZJM
     */
    showItem(val) {
      if (val.shiftUserCheckVoList.length === 0) {
        showWarningMessage(DEVICE_CHECK.NOT_CHECK_INFO);
        return false;
      }
      var day = moment(val.dayDateTime).format('DD');
      this.$emit('toggleHistroyItem', day, this.month);
    },
    /**
 * @description: 显示颜色
 * @param val 显示颜色
 * @return: 无
 * @author: CH
 */
    styleDict(item) {
      const newArr = item.shiftUserCheckVoList;
      if (newArr.length > 0) {
        let finishType = 0;// 是否点检
        for (let i = 0; i < newArr.length; i++) {
          // if (newArr[i].isFinish === 0) {
          //   finishType = 0; // 只要有一个班次未点检 则为未点检
          //   break;
          // } else if (newArr[i].isFinish === 2) {
          //   finishType = 2;// 只要有一个班次假日点检 则为假日点检
          //   break;
          // } else {
          //   finishType = 1; // 已点检
          // }
          if (newArr[i].userIdList.length > 0 && newArr[i].userIdList[0] !== -1) {
            finishType++;
          }
        }
        if (finishType === newArr.length) {
          return '#D4ECC8';
        } else if (finishType < newArr.length && finishType > 0) {
          return '#FFEFD4';
        } else {
          return '#FFFFFF';
        }
      } else {
        return '#FFFFFF';
      }
    },
    styleShift(item, timeStr) {
      if (item.isFinish) {
        if (item.shiftInformation.showColorList && item.shiftInformation.showColorList.length > 0) {
          var color = 'red';
          var colorList = item.shiftInformation.showColorList;
          for (var i = 0; i < colorList.length; i++) {
            var sTArr = colorList[i].startHHmm.split(':');
            var eTArr = colorList[i].endHHmm.split(':');
            var sNum = 0;
            var eNum = 0;

            var spNum = (Number(sTArr[0]) * 60 + Number(sTArr[1]));
            var epNum = (Number(eTArr[0]) * 60 + Number(eTArr[1]));

            var baseNum = new Date(timeStr.substring(0, 10) + ' 00:00:00').getTime();
            var now = new Date(item.shiftInformation.updateTime).getTime();
            if (spNum > epNum) {
              sNum = baseNum + ((Number(sTArr[0]) * 60 + Number(sTArr[1])) * 60 * 1000);
              eNum = baseNum + (24 * 60 * 60 * 1000) + ((Number(eTArr[0]) * 60 + Number(eTArr[1])) * 60 * 1000);
            } else {
              sNum = baseNum + ((Number(sTArr[0]) * 60 + Number(sTArr[1])) * 60 * 1000);
              eNum = baseNum + ((Number(eTArr[0]) * 60 + Number(eTArr[1])) * 60 * 1000);
            }
            if (sNum <= now && now <= eNum) {
              color = colorList[i].color;
            }
          }
          return color;
        } else {
          return 'green';
        }
      } else {
        return 'red';
      }
    },
    async showReviewCheck() {
      console.log(1111);
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'CHECK_REVIEW'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.isEnable === 1) {
            console.log(data.rows.isEnable);
            this.canReviewCheck = true;
          } else {
            console.log(data.rows.isEnable);
            this.canReviewCheck = false;
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    reviewText(item) {
      console.log(item);
      if (item.reviewState) {
        if (item.reviewState === 1) {
          return '【' + item.reviewedByName + '】审核通过';
        } else {
          return '【' + item.reviewedByName + '】审核不通过';
        }
      } else {
        return '未审核';
      }
    },
    getWeekdayFromDate(dateString) {
      const weekdays = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];
      const date = new Date(dateString);
      const dayOfWeek = date.getDay();
      return weekdays[dayOfWeek];
    }
  }
};
