<template>
  <el-container class="content">
    <el-main>
      <vxe-grid
        border="full"
        resizable
        keep-source
        ref="xGrid"
        class="vxe-table-element"
        height="350"
        :columns="tableColumns"
        :data="tableDatas"
        :edit-config="{
          trigger: 'click',
          mode: 'cell',
          showStatus: true,
          icon: 'fa fa-file-text-o'
        }"
      ></vxe-grid>
      <p style="text-align: left;" v-show="canReviewCheck" >审核情况：{{ reviewText() }} </p>
      <div class="checkInfo">
        <span>{{ DEVICE_CHECK.JOB_DESCRIPTION }}:</span>
        <el-input
          type="textarea"
          :placeholder="CONSTANTS_CODE.PLEASE_ENTER_CONTENT"
          v-model="checkRemarks"
          :rows="3"
          :disabled="true"
          maxlength="200"
          show-word-limit
        >
        </el-input>
        <el-upload
          list-type="picture-card"
          ref="upload"
          :disabled="true"
          :action="uploadConfig.url"
          :file-list="fileList"
          :on-preview="handlePictureCardPreview"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
      </div>
      <div class="btnDiv">
        <el-select v-model="selectedShift" @change="showItem">
          <el-option
            v-for="item in options"
            :key="item.shiftId"
            :label="
              item.shiftName +
              ':' +
              item.shiftStartTime +
              '~' +
              item.shiftEndTime
            "
            :value="item.shiftId"
          >
          </el-option>
        </el-select>
        <el-button
          v-show="canReviewCheck"
          type="primary"
          @click="checkReview"
          >审核</el-button
        >
        <el-button @click="goBack">{{ BTN_TEXT.RETURN }}</el-button>
        <el-date-picker
          :readonly="true"
          v-model="yearMonthDay"
          align="center"
          type="day"
          value-format="YYYY-MM-DD"
        ></el-date-picker>
      </div>

      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="500px"
        center>
        <span>请对本次点检任务进行审核</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="success" @click="checkReviewPass()">通过</el-button>
          <el-button type="danger" @click="checkReviewNoPass()">不通过</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/equipment/DeviceCheckManagement/checkHistroyItem';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/equipment/DeviceCheckManagement/checkHistroyItem.less';
</style>
