/*
 * @Descripttion:点检项列表
 * @version:
 * @Author: ZJM
 * @Date: 2020-06-17 10:45:31
 * @LastEditors: ZJM
 * @LastEditTime: 2022-01-26 21:58:49
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import moment from 'moment';
// 导入所需接口
import {
  checkGetListCheckListByDeviceCodeAction,
  checkGetListLastCheckListByDeviceCodeAction,
  checkUpdateCheckListAction,
  getCheckRepairTaskSwitchAction,
  insertCheckRepairTaskAction,
  delImgAction,
  checkReview,
  checkUnReview
} from '@/api/checkConfig';
import {
  configurationGetConfigurationByEnum
} from '@/api/system.js';
import {
  showSuccessMessage,
  showWarningMessage
} from '@/utils/message';
import { TITLE, STATUS_CODE, VARIABLE, BTN_TEXT, CONSTANTS_CHECK, DEVICE_CHECK, IP, FILE_TYPE, CONSTANTS_CODE } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
import { getUrlKey } from '@/utils/common';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
    // 设备code
    deviceCode: {
      type: String,
      default: ''
    },
    // 当前tab
    tabPosition: {
      type: String,
      default: 'now'
    }
  },
  data() {
    return {
      DEVICE_CHECK: DEVICE_CHECK,
      BTN_TEXT: BTN_TEXT,
      TITLE: TITLE,
      CONSTANTS_CODE: CONSTANTS_CODE,
      saveFalg: false,
      // 可编辑配置
      selectedShift: null,
      // 班次信息
      shiftData: {},
      // 当前班次
      nowShift: '',
      // 报修开关
      repairFlag: false,
      // 当前日期
      nowDate: '',
      options: [],
      // table数据
      tableDatas: [],
      // 报修备注
      repairRemark: [],
      // 报修弹窗
      dialogRepirVisible: false,
      // 点检工作描述
      checkRemarks: '',
      // table表头配置
      tableColumns: [
        {
          type: 'seq',
          title: VARIABLE.SEQ,
          align: 'center'
        },
        {
          field: 'itemName',
          title: CONSTANTS_CHECK.CHECK_ITEM,
          align: 'center'
        },
        {
          field: 'checkState',
          title: CONSTANTS_CHECK.IS_FINISH,
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              if (row.checkState === 0) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 1)}
                    class="icon-unChecked"
                  ></i>
                ];
              } else if (row.checkState === 1) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 2)}
                    class="icon-checked"
                  ></i>
                ];
              } else if (row.checkState === 2) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 0)}
                    class="icon-no"
                  ></i>
                ];
              } else if (row.checkState === 3) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 1)}
                    class="icon-stop"
                  ></i>
                ];
              } else if (row.checkState === 4) {
                return [
                  <i
                    onClick={() => this.toggleFinish(row, 1)}
                    class="icon-holiday"
                  ></i>
                ];
              }
            }
          }
        },
        {
          field: 'value',
          title: CONSTANTS_CHECK.REAL_VALUE,
          align: 'center',
          editRender: { name: 'input' }
        }
      ],
      // 图片上传
      relationshipId: '',
      dialogImageUrl: '',
      fileList: [],
      delFileList: [],
      dialogVisible: false,
      uploadConfig: {
        headers: {
          token: window.localStorage.getItem('token').replace(/"/g, '')
        },
        url: IP.PUBLICIP + 'fileResource/uploadOneFile.action',
        data: {
          fileResourceTypeEnum: FILE_TYPE.CHECK_RECORD_IMAGE,
          relationshipId: ''
        }
      },
      canReviewCheck: false,
      centerDialogVisible: false,
      checkRecord: {}
    };
  },
  computed: {
  },
  created() {
    this.getNowDate();
    this.showReviewCheck();
  },
  mounted() {
    // this.searchItem();
  },
  methods: {
    showRole: function(val) {
      const id = getUrlKey('id');
      var json = { ...this.$store.state.pageInfo[id] };
      var arr = json.buttonList;
      for (var i = 0; i < arr.length; i++) {
        if (val === Number(arr[i].type)) {
          return true;
        }
      }
      return false;
    },
    toggleFinish(row, value) {
      row.checkState = value;
    },
    editActivedEvent({ row, rowIndex }) {
      this.valueDisabled = true;
    },
    /**
     * @description: 获取点检项数据
     * @param {type}
     * @return:
     */
    async searchItem() {
      this.getCheckRepairTaskSwitch();
      this.$refs.xGrid.clearActived();
      this.fileList = [];
      this.saveFalg = false;
      this.checkRemarks = '';
      this.nowShift = '';
      this.tableDatas = [];
      this.options = [];
      this.selectedShift = null;
      if (this.deviceCode === '') {
        showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
        return false;
      }
      // 当前班次
      if (this.tabPosition === 'now') {
        try {
          const res = await checkGetListCheckListByDeviceCodeAction({
            deviceCode: this.deviceCode
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            if (!res.rows.templateId) {
              showWarningMessage(CONSTANTS_CHECK.NOW_NOTHING);
              return false;
            }
            for (var i in res.rows.checkList) {
              if (res.rows.checkList[i].checkState === 3 || res.rows.checkList[i].checkState === 4) {
                this.saveFalg = true;
              } else {
                this.saveFalg = false;
              }
            }
            this.shiftData = res.rows;
            // 将数据写入表单
            this.tableDatas = res.rows.checkList;
            this.selectedShift = res.rows.shiftId;
            this.checkRecord = res.rows.checkRecord;
            this.relationshipId = res.rows.checkRecord.recordId;
            this.checkRemarks = res.rows.checkRecord.checkInformation;
            this.nowShift = res.rows.shiftName + ':' + res.rows.shiftStartTime + '~' + res.rows.shiftEndTime;
            this.getFileList(res.rows.checkImageList);
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      } else if (this.tabPosition === 'prev') { // 上一班次
        try {
          const res = await checkGetListLastCheckListByDeviceCodeAction({
            deviceCode: this.deviceCode
          });
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            if (!res.rows.templateId) {
              showWarningMessage(CONSTANTS_CHECK.NOW_NOTHING);
              return false;
            }
            for (var s in res.rows.checkList) {
              if (res.rows.checkList[s].checkState === 3 || res.rows.checkList[s].checkState === 4) {
                this.saveFalg = true;
              } else {
                this.saveFalg = false;
              }
            }
            this.shiftData = res.rows;
            // 将数据写入表单
            this.tableDatas = res.rows.checkList;
            this.selectedShift = res.rows.shiftId;
            this.checkRecord = res.rows.checkRecord;
            this.relationshipId = res.rows.checkRecord.recordId;
            this.checkRemarks = res.rows.checkRecord.checkInformation;
            this.nowShift = res.rows.shiftName + ':' + res.rows.shiftStartTime + '~' + res.rows.shiftEndTime;
            if (res.rows.checkImageList) {
              this.getFileList(res.rows.checkImageList);
            }
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } catch (error) {
          showErrorNotify(TITLE.NOTIFY, error);
        } finally {
        }
      }
    },
    /**
     * @description: 当班全选
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    allSelect() {
      this.tableDatas = this.tableDatas.map(item => {
        item.checkState = 1;
        return item;
      });
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      // this.$refs.upload.clearFiles();
      // 时间戳
      // var time = new Date().getTime();

      // var startTime = new Date(this.tableDatas[0].checkStartTime).getTime();
      // var endTime = new Date(this.tableDatas[0].checkEndTime).getTime() + (this.shiftData.extensionTime * 1000);

      // if (startTime > time || time > endTime) {
      //   showWarningMessage(DEVICE_CHECK.CHECK_TIMEOUT);
      //   return false;
      // }
      this.repairRemark = [];

      // 当前时间
      // var now = Number(moment(time).format('HH') * 60) + Number(moment(time).format('mm'));
      // // 上班时间
      // var startTime = Number(this.shiftData.shiftStartTime.split(':')[0] * 60) + Number(this.shiftData.shiftStartTime.split(':')[1]);
      // // 下班时间
      // var endTime = Number(this.shiftData.shiftEndTime.split(':')[0] * 60) + Number(this.shiftData.shiftEndTime.split(':')[1]) + this.shiftData.extensionTime;

      // // 当上班时间小于下班时间
      // if (startTime < endTime) {
      //   // 若当前时间小于上班时间 或者 当前时间大于下班时间
      //   if (now < startTime || now > endTime) {
      //     showWarningMessage(DEVICE_CHECK.CHECK_TIMEOUT);
      //     return false;
      //   }
      // }
      // // 当上班时间大于下班时间
      // if (startTime > endTime) {
      //   // 若下班时间小于当前时间 并且 当前时间小于上班时间
      //   if (endTime < now || now < startTime) {
      //     showWarningMessage(DEVICE_CHECK.CHECK_TIMEOUT);
      //     return false;
      //   }
      // }
      // var checkStateIndex = 0;
      const arr = this.tableDatas.map(item => {
        var json = {
          id: item.id,
          value: item.value,
          checkState: item.checkState
        };
        if (item.checkState === 2) {
          this.repairRemark.push(item.itemName);
        }
        if (item.checkState === 0) {
          // checkStateIndex++;
        }
        return json;
      });

      // if (checkStateIndex > 0) {
      //   showWarningMessage('请完成所有点检项！');
      //   return false;
      // }

      const resourceIdListStr = this.fileList.map(item => {
        var json = '';
        if (!item.flag) {
          json = item.response.rows.resourceId;
        } else {
          json = item.id;
        }

        return json;
      });
      try {
        const res = await checkUpdateCheckListAction({
          recordId: this.relationshipId,
          checkInformation: this.checkRemarks,
          resourceIdListStr: resourceIdListStr.toString()
        }, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var isAllCheck = true;
          for (var i in arr) {
            if (arr[i].checkState === 0) {
              isAllCheck = false;
            }
          }
          if (this.delFileList.length !== 0) {
            this.delImg();
          }

          if (this.repairRemark.length !== 0) {
            if (this.repairFlag) {
              this.dialogRepirVisible = true;
            }
          }
          // 若为当前班次，则改变设备树状态
          if (this.tabPosition === 'now') {
            this.$emit('changeNode', isAllCheck);
          }
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 点击设备免检
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    holidayFree() {
      this.$emit('holidayFree');
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    },
    getNowDate() {
      var _that = this;
      _that.nowDate = moment(new Date().getTime()).format('YYYY-MM-DD');
      setInterval(() => {
        _that.nowDate = moment(new Date().getTime()).format('YYYY-MM-DD');
      }, 500);
    },
    /**
     * @description: 图片控制
     * @param 无
     * @return:无
     * @author: ZJM
     */
    handleRemove(file, fileList) {
      if (!file.flag) {
        this.delFileList.push(file.response.rows.resourceId);
        this.fileList = fileList;
      } else {
        this.delFileList.push(file.id);
        this.fileList = fileList;
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    updateFiles() {
      if (!this.deviceCode) {
        showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
        return false;
      }
    },
    updateSuccess(response, file, fileList) {
      this.fileList.push(file);
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        // this.fileList.push({
        //   id: response.rows.resourceId,
        //   url: IP.PUBLICIP + response.rows.fileUrl,
        //   relationshipId: response.rows.relationshipId
        // });
      }
    },
    getFileList(imgFIle) {
      const arr = imgFIle.map(item => {
        var json = {
          id: item.resourceId,
          url: IP.PUBLICIP + item.fileUrl,
          relationshipId: item.relationshipId,
          flag: true
        };
        return json;
      });
      this.delFileList = [];
      this.fileList = arr;
    },
    /**
    * @description: 删除图片
    * @param {type}
    * @return:
    */
    async delImg() {
      try {
        const res = await delImgAction({ ids: this.delFileList }, []);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.delFileList = [];
          // 提示正常信息
          // showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
    * @description: 恢复
    * @param {type}
    * @return:
    */
    recovery() {
      this.saveFalg = false;
    },
    /**
    * @description: 停机
    * @param {type}
    * @return:
    */
    async halt() {
      this.$refs.xGrid.clearActived();
      // this.$refs.upload.clearFiles();
      // 时间戳
      var time = new Date().getTime();

      var startTime = new Date(this.tableDatas[0].checkStartTime).getTime();
      var endTime = new Date(this.tableDatas[0].checkEndTime).getTime() + (this.shiftData.extensionTime * 1000);

      if (startTime > time || time > endTime) {
        showWarningMessage(DEVICE_CHECK.CHECK_TIMEOUT);
        return false;
      }

      // 当前时间
      // var now = Number(moment(time).format('HH') * 60) + Number(moment(time).format('mm'));
      // // 上班时间
      // var startTime = Number(this.shiftData.shiftStartTime.split(':')[0] * 60) + Number(this.shiftData.shiftStartTime.split(':')[1]);
      // // 下班时间
      // var endTime = Number(this.shiftData.shiftEndTime.split(':')[0] * 60) + Number(this.shiftData.shiftEndTime.split(':')[1]) + this.shiftData.extensionTime;

      // // 当上班时间小于下班时间
      // if (startTime < endTime) {
      //   // 若当前时间小于上班时间 或者 当前时间大于下班时间
      //   if (now < startTime || now > endTime) {
      //     showWarningMessage(DEVICE_CHECK.CHECK_TIMEOUT);
      //     return false;
      //   }
      // }
      // // 当上班时间大于下班时间
      // if (startTime > endTime) {
      //   // 若下班时间小于当前时间 并且 当前时间小于上班时间
      //   if (endTime < now || now < startTime) {
      //     showWarningMessage(DEVICE_CHECK.CHECK_TIMEOUT);
      //     return false;
      //   }
      // }

      const arr = this.tableDatas.map(item => {
        var json = {
          id: item.id,
          value: item.value,
          checkState: 3
        };
        return json;
      });
      const resourceIdListStr = this.fileList.map(item => {
        var json = '';
        if (!item.flag) {
          json = item.response.rows.resourceId;
        } else {
          json = item.id;
        }

        return json;
      });
      try {
        const res = await checkUpdateCheckListAction({
          recordId: this.relationshipId,
          checkInformation: this.checkRemarks,
          resourceIdListStr: resourceIdListStr.toString()
        }, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var isAllCheck = true;
          for (var i in arr) {
            if (arr[i].checkState === 0) {
              isAllCheck = false;
            }
          }
          if (this.delFileList.length !== 0) {
            this.delImg();
          }
          // 若为当前班次，则改变设备树状态
          if (this.tabPosition === 'now') {
            this.$emit('changeNode', isAllCheck);
          }
          this.searchItem();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
    * @description: 获取报修异常开关
    * @param {type}
    * @return:
    */
    async getCheckRepairTaskSwitch() {
      try {
        const res = await getCheckRepairTaskSwitchAction({}, []);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.repairFlag = res.rows;
          // 提示正常信息
          // showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          // showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
    * @description: 生成维修单
    * @param {type}
    * @return:
    */
    async insertCheckRepairTask() {
      try {
        const res = await insertCheckRepairTaskAction({
          deviceCode: this.deviceCode,
          repairRemark: this.repairRemark
        }, []);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.dialogRepirVisible = false;
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async showReviewCheck() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'CHECK_REVIEW'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.isEnable === 1) {
            this.canReviewCheck = true;
          } else {
            this.canReviewCheck = false;
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    checkReview() {
      if (this.checkRecord.checkState === 0) {
        showWarningMessage('未完成点检，无法进行审核！');
      } else {
        this.centerDialogVisible = true;
      }
    },
    async checkReviewPass() {
      try {
        const data = await checkReview({}, [this.relationshipId]);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.centerDialogVisible = false;
          showSuccessMessage(data.message);
          this.searchItem();
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async checkReviewNoPass() {
      try {
        const data = await checkUnReview({}, [this.relationshipId]);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.centerDialogVisible = false;
          showSuccessMessage(data.message);
          this.searchItem();
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    reviewText() {
      if (this.checkRecord.reviewState) {
        if (this.checkRecord.reviewState === 1) {
          return '【' + this.checkRecord.reviewedByName + '】审核通过';
        } else {
          return '【' + this.checkRecord.reviewedByName + '】审核不通过';
        }
      } else {
        return '未审核';
      }
    }
  }
};
