<template>
  <el-container class="content">
    <el-main>
      <vxe-grid
        border="full"
        resizable
        keep-source
        ref="xGrid"
        class="vxe-table-element"
        height="350"
        :columns="tableColumns"
        :data="tableDatas"
        :edit-config="{
          trigger: 'click',
          mode: 'cell',
          showStatus: false,
          icon: 'fa fa-file-text-o'
        }"
        @edit-actived="editActivedEvent"
      ></vxe-grid>
      <p style="text-align: left;" v-show="canReviewCheck" >审核情况：{{ reviewText() }} </p>
      <div class="checkInfo">
        <span>{{ DEVICE_CHECK.JOB_DESCRIPTION }}:</span>
        <el-input
          type="textarea"
          :placeholder="CONSTANTS_CODE.PLEASE_ENTER_CONTENT"
          v-model="checkRemarks"
          :rows="3"
          :disabled="tableDatas.length === 0 || saveFalg"
          maxlength="200"
          show-word-limit
        >
        </el-input>
        <el-upload
          list-type="picture-card"
          ref="upload"
          :disabled="tableDatas.length === 0 || saveFalg"
          :headers="uploadConfig.headers"
          :action="uploadConfig.url"
          :data="uploadConfig.data"
          :before-upload="updateFiles"
          :file-list="fileList"
          :on-success="updateSuccess"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
      </div>
      <div class="btnDiv">
        <el-button
          type="danger"
          v-show="showRole(2)"
          :disabled="tableDatas.length === 0 || saveFalg"
          @click="holidayFree"
          >{{ DEVICE_CHECK.HOLIDAY_FREE }}</el-button
        >
        <el-button
          :disabled="tableDatas.length === 0"
          v-show="saveFalg"
          @click="recovery"
          >{{ BTN_TEXT.RECOVERY }}</el-button
        >
        <el-button
          v-show="!saveFalg"
          :disabled="tableDatas.length === 0 || saveFalg"
          @click="halt"
          >{{ BTN_TEXT.HALT }}</el-button
        >
        <el-input class="shiftNow" v-model="nowShift" :readonly="true">
        </el-input>
        <el-button
          v-show="showRole(3)"
          :disabled="tableDatas.length === 0 || saveFalg"
          @click="allSelect"
          >{{ DEVICE_CHECK.ALL_SELECT }}</el-button
        >
        <el-button
          v-show="showRole(4)"
          :disabled="tableDatas.length === 0 || saveFalg"
          type="primary"
          @click="submit"
          >{{ BTN_TEXT.SAVE }}</el-button
        >
        <el-button
          v-show="canReviewCheck"
          type="primary"
          :disabled="tableDatas.length === 0 || saveFalg"
          @click="checkReview"
          >审核</el-button
        >
        <el-input class="nowDate" v-model="nowDate" :readonly="true">
        </el-input>
      </div>
      <el-dialog
        :title="TITLE.PROMPT"
        :visible.sync="dialogRepirVisible"
        width="30%"
      >
        <span>{{ DEVICE_CHECK.INSERT_CHECK_REPAIRTASK }}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogRepirVisible = false">{{
            BTN_TEXT.CLOSE
          }}</el-button>
          <el-button type="primary" @click="insertCheckRepairTask">{{
            BTN_TEXT.CONFIRM
          }}</el-button>
        </span>
      </el-dialog>

      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="500px"
        center>
        <span>请对本次点检任务进行审核</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="success" @click="checkReviewPass()">通过</el-button>
          <el-button type="danger" @click="checkReviewNoPass()">不通过</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/equipment/DeviceCheckManagement/checkItemList';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/equipment/DeviceCheckManagement/checkItemList.less';
</style>
