<template>
  <el-container class="content">
    <el-main>
      <div class="histroyContent">
        <div
          v-for="(item, index) in tableDatas"
          :key="index"
          @click="showItem(item)"
          class="histroyDiv"
        >
          <div
            class="cardBackground"
            :style="{ 'background-color': styleDict(item) }"
          >
            <div style="text-align: center;" >{{ moment(item.dayDateTime).format('DD')  }}<span style="margin-left: 10px;" >{{ getWeekdayFromDate(item.dayDateTime) }}</span></div>
            <div
              v-for="(shift, index) in item.shiftUserCheckVoList"
              :key="index"
              class="shiftDiv"
            >
              <span :style="{ 'color': styleShift(shift, item.dayDateTime) }"
                >{{ shift.shiftName }}:{{
                  shift.realNameList.join(',') === ''
                    ? VARIABLE.NOT_HAVE
                    : shift.realNameList.join(',')
                }}<span style="margin-left: 10px;" v-if="canReviewCheck" >{{ reviewText(shift.checkRecord) }}</span></span>
            </div>
          </div>
        </div>
      </div>
      <div class="monthContent">
        <el-date-picker
          :clearable="false"
          :align="'center'"
          v-model="month"
          type="month"
          value-format="yyyy-MM"
          placeholder="选择月"
          @change="searchItem"
        ></el-date-picker>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/equipment/DeviceCheckManagement/checkHistroy';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/equipment/DeviceCheckManagement/checkHistroy.less';
</style>
