/*
 * @Descripttion: 设备点检主页面js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-21 15:26:48
 * @LastEditors: ZJM
 * @LastEditTime: 2022-10-26 19:54:28
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import sHeader from '@/components/common/StationHeader/header.vue';
import HeaderAction from '@/components/common/StationHeader/headerAction.vue';
import moment from 'moment';
// 引入ztree树形组件
import ZTree from '@/components/common/ZTree/ZTreeCheck';
// 引入提示组件
import {
  showSuccessMessage,
  showWarningMessage
} from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { TITLE, STATUS_CODE, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION, DEVICE_CHECK, WORK_STATION } from '@/utils/constantsFile';
// 引入类型信息组件
import checkItemList from '@/views/equipment/DeviceCheckManagement/checkItemList.vue';
import checkHistroy from '@/views/equipment/DeviceCheckManagement/checkHistroy.vue';
import checkHistroyItem from '@/views/equipment/DeviceCheckManagement/checkHistroyItem.vue';
// 导入所需接口
import {
  checkGetListGroupTreeDeviceCheckByCurrentUserAction,
  checkUpdateHolidayInspectionFreeAction,
  checkGetListGroupTreeDeviceCheckAction
} from '@/api/checkConfig';
import { getUrlKey } from '@/utils/common';
export default {
  name: 'DeviceCheckManagement',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载树形组件
    ZTree,
    // 加载组件
    checkItemList,
    checkHistroy,
    checkHistroyItem,
    sHeader,
    HeaderAction
  },
  data() {
    return {
      WORK_STATION: WORK_STATION,
      DEVICE_CHECK: DEVICE_CHECK,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      // 是否显示历史详情
      showItem: false,
      // table标题
      HeadTitleName: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_LIST,
      tableName: 'device',
      // tab切换
      tabPosition: 'now',
      // 新增时显示模板选择
      showModelItem: false,
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // ztree对象
      ztreeObj: {},
      // ztree选中节点对象
      ztreeSelectedNode: {
        id: '-1',
        pId: '-1'
      },
      // ztree树形数据
      nodeData: [],
      // 历史点击的日期
      histroyDay: '',
      // 历史日期年-月
      histroyMonth: ''
    };
  },
  computed: {},
  created() {
    // 调用查询树形数据接口
    this.getTree();
  },
  watch: {
    // 切换车间分组和设备类型查询
    tabPosition(val) {
      this.showItem = false;
      // 当前班次
      if (val === 'now') {
        setTimeout(() => {
          this.$refs.checkItemList.searchItem();
        }, 0);
      } else if (val === 'prev') { // 上一班次
        setTimeout(() => {
          this.$refs.checkItemList.searchItem();
        }, 0);
      } else if (val === 'histroy') { // 点检历史
        setTimeout(() => {
          this.$refs.checkHistroy.searchItem();
        }, 0);
      }
    }
  },
  methods: {
    showRole: function(val) {
      const id = getUrlKey('id');
      console.log(id);
      var json = { ...this.$store.state.pageInfo[id] };
      console.log('666', json.buttonList);
      var arr = json.buttonList;
      if (json.buttonList) {
        for (var i = 0; i < arr.length; i++) {
          if (val === Number(arr[i].type)) {
            return true;
          }
        }
      }
      return false;
    },
    /**
     * @description: ztree单击事件
     * @param event 事件对象
     * @param nodes 选中数据对象
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // 获取选中数据
      this.ztreeSelectedNode = nodes;
      // 获取ztree对象
      this.ztreeObj = obj;
      // 当前班次
      if (this.tabPosition === 'now') {
        setTimeout(() => {
          this.$refs.checkItemList.searchItem();
        }, 0);
      } else if (this.tabPosition === 'prev') { // 上一班次
        setTimeout(() => {
          this.$refs.checkItemList.searchItem();
        }, 0);
      } else if (this.tabPosition === 'histroy') { // 点检历史
        // 是否显示点检详情
        if (this.showItem) {
          setTimeout(() => {
            this.$refs.checkHistroyItem.searchItem();
          }, 0);
        } else { // 显示点检历史
          setTimeout(() => {
            this.$refs.checkHistroy.searchItem();
          }, 0);
        }
      }
    },
    /**
     * @description: ztree初始化完成事件
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // 获取ztree对象
      this.ztreeObj = obj;
    },
    /**
     * @description: 获取类型树数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getTree() {
      try {
        if (this.$route.query.isWorkstation) {
          const res = await checkGetListGroupTreeDeviceCheckByCurrentUserAction();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 将数据写入表单
            this.nodeData = res.rows.map(item => {
              if (item.code) {
                if (item.type === 1) {
                  item.iconSkin = 'yes';
                  item.isChecked = true;
                } else if (item.type === 0) {
                  item.iconSkin = 'no';
                  item.isChecked = false;
                }
              }
              return item;
            });
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        } else {
          const res = await checkGetListGroupTreeDeviceCheckAction();
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 将数据写入表单
            this.nodeData = res.rows.map(item => {
              console.log(666, item);
              if (item.code) {
                if (item.type === 1) {
                  item.iconSkin = 'yes';
                  item.isChecked = true;
                } else if (item.type === 0) {
                  item.iconSkin = 'no';
                  item.isChecked = false;
                }
              }
              return item;
            });
          } else {
            // 提示异常信息
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 改变设备的点检状态
     * @param param 切换值
     * @return: 无
     * @author: ZJM
     */
    changeNode(param) {
      if (!param) {
        this.ztreeSelectedNode.iconSkin = 'no';
        this.ztreeSelectedNode.isChecked = false;
      } else {
        this.ztreeSelectedNode.iconSkin = 'yes';
        this.ztreeSelectedNode.isChecked = true;
      }
      this.ztreeObj.updateNode(this.ztreeSelectedNode);
    },
    /**
     * @description: 切换显示历史和历史详情
     * @param day 日期
     * @param month 年月
     * @return: 无
     * @author: ZJM
     */
    toggleHistroyItem(day, month) {
      this.histroyDay = day + '';
      this.histroyMonth = month + '';
      this.showItem = !this.showItem;
      if (this.showItem) {
        setTimeout(() => {
          this.$refs.checkHistroyItem.searchItem();
        }, 0);
      } else {
        setTimeout(() => {
          this.$refs.checkHistroy.searchItem();
        }, 0);
      }
    },
    /**
     * @description: 设备假日免检
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async holidayFree() {
      this.$confirm(DEVICE_CHECK.COMFIRM_HOLIDAY_FREE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setHolidayFree();
        })
        .catch(() => { });
    },
    /**
     * @description: 执行设备假日免检
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setHolidayFree() {
      try {
        const res = await checkUpdateHolidayInspectionFreeAction({
          date: moment(new Date().getTime()).format('YYYY-MM-DD') + ' 00:00:00',
          deviceCode: this.ztreeSelectedNode.code
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          showSuccessMessage(res.message);
          this.changeNode(true);
          this.$refs.checkItemList.searchItem();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 全部设备假日免检
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async holidayAllFree() {
      this.$confirm(DEVICE_CHECK.COMFIRM_HOLIDAY_ALL_FREE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setHolidayAllFree();
        })
        .catch(() => { });
    },
    /**
     * @description: 执行全部设备假日免检
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setHolidayAllFree() {
      try {
        const res = await checkUpdateHolidayInspectionFreeAction({
          date: moment(new Date().getTime()).format('YYYY-MM-DD') + ' 00:00:00'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          showSuccessMessage(res.message);
          for (var item in this.nodeData) {
            if (this.nodeData[item].code) {
              const node = this.ztreeObj.getNodeByParam('code', this.nodeData[item].code, null);
              node.iconSkin = 'yes';
              node.isChecked = true;
              this.ztreeObj.updateNode(node);
            }
          }
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    goHome() {
      this.$router.replace('/workstation/home');
    }
  }
};
